.App {
  text-align: center;
}
/*------------------------------*/
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
   border-top: 2px solid #1292bb;
   border-bottom: 1.5em solid #1292bb
}

body {
  margin: 0;
  /*font-family: "Open Sans", sans-serif;*/
  font-family: 'Rubik', sans-serif;
  color: #58595b;
  padding: 10px;

}

a {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  color: #1292bb;
}

.site-log {
  width: 10%;
  height: auto;
}

h1,
h2 {
  font-family: 'Rubik', sans-serif;
}

h1 a {
  color: #58595b;
}

h1 a:hover {
  color: #1292bb;
}

h1, .section-logo {
  font-size: calc(16px + 1.25vw);
}

h2 {
  font-size: calc(14px + 1vw);
}

p {
  text-align: justify;
}



footer {
  padding-top: 1.5em;
  text-align: center;
  font-size: 2em;
}

.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  border-color: #7d97ad;
  margin-bottom: 8px;
  margin-top: 8px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.landing {
    padding: 2em 0.5em;

  }

.skills-container {
  width: 100%;
  padding: 0.5em 0.6em 1em;
  display: flex;
  flex-wrap: wrap;
}

.skills-container h1{
  width: 100%;
  text-align: center;
}

.skills-container-item {
    border-bottom: 2px solid  #1292bb;
    width: 100%;
    background-color: white;
  }

.skills-container-item:first-of-type {
  border-top: 2px solid  #1292bb;
}

.skill-list {
  list-style-type: none;
  padding: 1em;
}

.skill-list li {
  padding: 0.4em;
}

header h3 {
  margin: 4px;
}

.app-header {
  width: 100%;
  text-align: center;
}

nav .nav-link{
  color: inherit;
  padding: 2px 6px;
  font-size: 1.1em;
}

nav .nav-link:hover {
  color: #1292bb;
}

.active-navlink {
  color: #1292bb;
  border-bottom: 3px solid #1292bb;
}

.main-img-div {
  width: 100%;
}

.desc {
  width: 100%;
  font-size: 1em;
}

.featured {
    padding: 3.5em 1em;
}

.featured-text,
.featured-head {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.featured-head {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
    justify-content: center;
    margin: 6px 0;
}

.featured-head > h2 {
  text-align: center;
  width: 100%;
  margin: 0;
}

.featured-link {
  height: 2em;
  border: 2px solid #7D7D7D;
  border-radius: 3px;
  color: #7D7D7D;
  background-color: white;
  margin: 0 8px;
  font-size: 14px;
}

.featured-link > a {
  padding: 0;
  color: inherit;
}

.featured-link:hover {
  color: white;
  background-color: #1292bb;
  border-color: #1292bb;
  cursor: pointer;
}
.featured-img {
  max-width: 100%;
  border-radius: 4px;
  /*margin: 0 10px;*/
}

.featured-skill {
  height: 2.5em;
  color: white;
  background-color: #7D7D7D;
  border: 1px solid #7D7D7D;
  border-radius: 3px;
  margin-right: 6px;
  margin-bottom: 6px;
  font-size: 11px;
}

.flex-project {
  float: left;
  width: 100%;
  border: thin solid #ccc;
  border-radius: 2%;
  text-align: center;
  padding: 10px;
  margin-bottom: 5px;
}

.proj-title {
  font-weight: bold;
  margin-bottom: 4px;
}

.main-img {
  max-height: 160px;
  width: auto;
  display: block;
  border-radius: 50%;
  margin: auto;
  padding: 16px;
}

.proj-img {
  max-width: 100%;
  border-radius: inherit;
}

.proj-links {
  margin: 0;
  padding: 0;
  text-align: center;
}

footer a .social{
  margin: 4px;
  color: #58595b;
}

footer a .social:hover {
  color: #1292bb;
}

.certificate-img {
  max-height: 160px;
  width: auto;
  }

  .certificate-img:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  .certificate p {
    text-align: center;
  }

@media only screen and (min-width: 768px) {
  body {
    margin: 10px 10px;
  }

  .main-img {
    width: auto;
    height: auto;
  }

  .featured-head > h2 {
    text-align: left;
    width: calc(80% - 32px);
  }

  .featured-link {
    height: 3em;
    text-align: center;
    width: 10%;
    -ms-flex-item-align: end;
        align-self: flex-end;
  }

  .featured {
    padding: 3.5em 1em;
  }

  .featured-text {
    padding: 0 10px;
  }

  .desc {
    text-align: center;
    font-size: 1.2em;
  }

  .flex-project {
    width: calc((100% - 48px)/3);
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-style: none;
    margin: 8px;
    min-height: 355px;
  }

  .skills-container {
    width: 100%;
    background-color: #b1deecc7;
    padding: 0.5em;
    margin: 2.5em auto 4.5em;
    display: flex;
    flex-wrap: wrap;
    border-radius: 4px;
  }
  .skills-container-item {
    margin: 4px;
    width: calc((100% - 24px) / 3);
    border: none;
  }

  .skills-container-item:first-of-type {
    border-top: none;
  }

  .certificate-img {
    max-height: 200px;
    width: auto;
    }
   .certificates-container {
      margin: 3em auto 4em;
    }

}

@media only screen and (min-width: 992px) {

  .app-header {
    padding: 20px;
    margin-bottom: 2em;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  header h1 {
    width: 75%;
    text-align: left;
  }

  nav .nav-link{
    padding: 2px 8px;
    width: calc(25% / 2);
    font-size: 1.3em;
  }

  h2 {
    font-size: 24px;
  }

  .landing {
    padding: 2em 0;

  }

  .main-img {
    max-height: 260px;
  }

  .desc {
    padding: 10px 10em;
    font-size: 1.4em;
  }

  .featured {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    padding: 2em 2em 3em;
    justify-content: center;
    align-items: flex-start;
  }

  .featured picture {
    width: 65%;
    padding-right: 3em;
  }

  .featured-text {
    width: 35%;
  }

  .featured-head > h2 {
    width: 100%;
    text-align: center;
    padding: 0.5em 0 0.75em;
  }

  .featured-link {
    width: auto;
  }

  .flex-project {
    min-height: 364px;
  }

  .skills-container {
    width: 90%;
    margin: 2.5em auto 4em;
    display: flex;
    flex-wrap: wrap;
    border-radius: 4px;
    /*margin: auto;*/
  }

  .certificates-container {
    width: 90%;
    margin: 3em auto 4.5em;
    display: flex;
    align-items: center;
  }

  .certificate {
    width: 50%;
    border-radius: 6px;
    background-color: white;
    border-radius:
  }

  .certificate-img {
    max-height: 250px;
    width: auto;
    }

}

@media only screen and (min-width: 1200px) {

  body {
    margin: 10px auto;
    max-width: 88vw;
  }

  h2 {
    font-size: calc(16px + 0.75vw);
  }

  .landing-page {
    min-height: 100vh;
  }

  .flex-project {
    width: calc((100% - 90px)/3);
    margin: 15px;
    min-height: 386px;
    padding: 15px;
  }

   .skills-container-item {
    margin: 1em;
    width: calc((100% - 6em) / 3);
    border: none;
  }

}

@media only screen and (min-width: 1400px) {
  .landing-page {
    min-height: 0;
  }

  body {
    max-width: 72vw;
  }
}

